.main-nav__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
      display: block;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6;
      cursor: pointer;
      border: none; } }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 13px;
  width: 23px;
  height: 1px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000; }

.main-nav--opened .main-nav__toggle {
  z-index: 20;
  background-color: #ffffff; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 25px;
    left: 8px;
    width: 34px;
    height: 1px;
    background-color: rgba(20, 21, 23, 0.2); }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  margin: 0;
  padding: 21px 25px 9px 21px;
  list-style: none; }
  .main-nav__list::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.2); }
    @media (max-width: 1023px) {
      .main-nav__list::before {
        display: none; } }

.main-nav__item {
  font-weight: 300; }
  @media (max-width: 1023px) {
