@media (max-width: 1023px) {
  .main-nav--closed .main-nav__item {
    display: none; }
  .main-nav--opened .main-nav__list {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    padding: 100px 0;
    z-index: 2;
    background-color: #ffffff;
    position: absolute; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
